.nft {
  border-radius: 16px;
}
.nft__link {
  position: relative;
  display: flex;
  cursor: pointer;
  margin-bottom: 1.125rem;
}
.nft__link::before {
  border-radius: 0.625rem;
  content: "";
  background-color: var(--cyan);
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.nft__link:hover::before {
  opacity: 0.6;
  border-radius: 0.625rem;
}
.nft__link::after {
  border-radius: 0.625rem;
  content: "";
  background: url(../images/icon-view.svg);
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.nft__link:hover::after {
  opacity: 1;
}
.nft__image {
  border-radius: 0.625rem;
  width: 100%;
}
.nft__name:hover, .nft__creator--name:hover {
  cursor: pointer;
  color: var(--cyan);
}
.nft__name {
  margin-bottom: 1rem;
}
.nft__description {
  color: var(--soft-blue);
  line-height: 1.4;
  margin-bottom: 1.5rem;
}
.nft__value {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--very-dark-blue-line);
  margin-bottom: 1rem;
}
.nft__price {
  color: var(--cyan);
  letter-spacing: 0.1rem;
  font-size: 1rem;
  display: flex;
  font-weight: bold;
  align-items: center;
}
.nft__price img {
  margin-right: 0.5rem;
}
.nft__time {
  color: var(--soft-blue);
  display: flex;
  align-items: center;
  font-size: 1rem;
}
.nft__time img {
  margin-right: 0.5rem;
}
.nft__creator {
  display: flex;
  align-items: center;
}
.nft__creator--image {
  border: 1px solid var(--white);
  border-radius: 50%;
  width: 32px;
  margin-right: 1rem;
}
.nft__creator--sponsor {
  color: var(--soft-blue);
}
.nft__creator--name {
  font-size: 1rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background: var(--background);
  color: var(--text-color);
  font-family: var(--font-outfit);
  font-size: 14px;
  margin: 0 1rem;
  min-height: calc(100vh - 0.1px);
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 2rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: var(--cyan);
}

:root {
  --soft-blue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --Very-dark-blue-main-bg: hsl(217, 54%, 11%);
  --very-dark-blue-card-bg: hsl(216, 50%, 16%);
  --very-dark-blue-line: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
  --background: var(--Very-dark-blue-main-bg);
  --text-color: var(--white);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  background: var(--very-dark-blue-card-bg);
  max-width: 350px;
  padding: 1.5rem;
}
@media (min-width: 40em) {
  .container {
    padding: 1.375rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-fraunces);
}

p {
  font-size: 1.125rem;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}