@use "../util/" as *;

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  background: var(--very-dark-blue-card-bg);
  max-width: 350px;
  padding: rem(24);

  @include breakpoint(medium) {
    padding: rem(22);
  }
}
